const DB_NAME = 'swaab';

function getDBRef (db) {
    return new Promise((resolve, reject) => {
        const req = indexedDB.open(db, 1)
    
        req.addEventListener('upgradeneeded', () => {
            req.result.createObjectStore('domains', { keyPath: 'domain' })
        })

        req.addEventListener('error', reject)
        req.addEventListener('success', () => resolve(req.result))
    })
}

export function addDomain(domain) {
    return new Promise((resolve, reject) => {
        getDBRef(DB_NAME).then((db) => {
            const domains = db.transaction(['domains'], 'readwrite').objectStore('domains')
        
            const request = domains.put({
                domain: domain,
                createdAt: new Date().getTime(),
            })

            request.addEventListener('success', resolve)
            request.addEventListener('error', reject)
        })
    })
}

function getDomains() {
    return new Promise((resolve, reject) => {
        getDBRef(DB_NAME).then((db) => {
            const domains = db.transaction(['domains'], 'readwrite').objectStore('domains')

            const request = domains.getAll()

            request.addEventListener('error', reject)
            request.addEventListener('success', () => resolve(request.result.map(({ domain }) => domain)))
        })
    })
}

function getRandomURI(subReq = 0) {
    const withSubReq = subReq < 7 && Math.random() > 0.3
    const randomString = Math.random().toString(36).slice(2, 3 + parseInt(Math.random() * 9, 10))

    return `${randomString}${withSubReq ? `/${getRandomURI(subReq + 1)}` : ''}`;
}

export async function ultrafetch(url, options) {
    const domains = await getDomains()

    for (let domain of domains) {
        try {
            return await fetch(`https://${domain}/${getRandomURI()}`, {
                method: options.method || 'get',
                credentials: 'include',
                body: options.body,
                headers: {
                    token: btoa(url),
                },
            })
        } catch (e) {}
    }

    throw new Error('AAB Request Failed');
}

export async function testPingDomain(domain) {
    try {
        const request = await fetch(domain.indexOf(':') > -1 ? domain : `https://${domain}`)
        const { status } = await request.json()
        return status === false
    } catch (e) {
        return false
    }
}