export function isGoogleLighthouse() {
    const ua = navigator.userAgent
    const gLReg = new RegExp(/Chrome-Lighthouse/)

    return gLReg.test(ua)
}

const MAX_INCOGNITO_QUOTA = 1200000000

export function isGoogleChromeIncognito() {
    const errorHandler = () => {
        console.debug('error')
    }
    function onInitFs(fs) {
        console.debug(fs)
        console.debug(fs.root)


    }

    window.webkitRequestFileSystem(window.PERSISTENT, 1024*1024*1024*700, onInitFs, errorHandler);

    return new Promise((resolve, reject) => {
        if (navigator.storage && navigator.storage.estimate) {
            navigator.storage.estimate().then((data) => {
                console.debug('quota', data)
                if (data.quota < MAX_INCOGNITO_QUOTA) {
                    resolve()
                } else {
                    reject(false)
                }
            })
        } else {
            reject(false)
        }
    })

}