// @flow

import { ultrafetch } from "../helpers/antiadblock";

const AAB_TIMEOUT_MESSAGE = 'AAB BroadcastRequest timeout'
export const AAB_BROADCAST_REQUEST_TIMEOUT = new Error(AAB_TIMEOUT_MESSAGE)
export const BROADCAST_CHANNEL_ERROR = new Error("BroadcastChannel is not supported")

export default function aabfetch(url: string, options:RequestOptions = {}, fallbackEnabled: boolean = true): Promise<Object> {
    return fetch(url, options).catch(e => {
        if (fallbackEnabled) {
            return ultrafetch(url, options)
        }

        try {
            // i think message here in most cases: "TypeError: Failed to fetch"
            e.message += ` url=${url} body=${JSON.stringify(options.body||"")}`
        } catch(_) { }


        throw e
    })
}