// @flow


export function getOptionsFromUrl(u: URL): $Shape<Options>  {
    const urlParams = u.search.slice(1).split('&').reduce((urlParams, kw) => {
        const [k, v] = kw.split('=')
        urlParams[k] = v
        return urlParams
    }, {})

    const supportedParamsMap = [
        ["cdn", "cdn", Boolean],
        ["domain", "domain", String],
        ["var", "var", String],
        ["ymid", "ymid", String],
        ["var_3", "var_3", String],
        ["m", "forcePopup", (m) => m === "http"],
        ["m", "forceStandalone", (m) => m === "https"],
        ["dnd", "notificationsDelayHttps", Number],
        ["dsws", "disableSwSanity", Boolean],
        ['mpcd', "afterCloseDelay", Number],
        ['zoneId', "zoneId", Number],
        ['zoneid', "zoneId", Number],
        ["p", "zoneId", Number],
        ['z', "zoneId", Number],
        ["rtmu", "useRtMarkUser", rtmu => rtmu === 'true'],
        ["sw", "swName", String],
        ["gt", "gidratorTimeout", Number],
        ["pub", "pub", pub => Number(pub) || 0],
        ["t", "t", String],
        ["autoInstall", "autoInstall", x => x ? !(x === "false"): true],
        ["exp", "experiment", String],
        ["dsig", "dsig", String],
    ]

    const optionsOverride = ((supportedParamsMap.reduce((optionsOverride, [name, optName, converter]) => {
        if (urlParams[name] !== undefined) {
            optionsOverride[optName] = converter(urlParams[name])
        }

        return optionsOverride
    }, {}): any): $Shape<Options>)

    return optionsOverride
}
