// @flow
import fetch from "../../CommonLibraries/network/aabfetch"
import {broadcastInfo, unBroadcastAllInfo} from "../../CommonLibraries/functions/broadcastInfo"
import type {TagDataSet} from "./types"
import {zoneVersion} from "./types"
import {getOptionsFromUrl} from "./getOptionsFromUrl"
import type {SDKType} from "../sdk"
import {delay} from "../../CommonLibraries/helpers/delay"
import {inject} from "../../CommonLibraries/helpers/inject"
import {swPingDomain,swVersion} from '../consts'
import { testPingDomain } from "../../CommonLibraries/helpers/antiadblock";

declare var sdk_: SDKType

function parseZoneFromDocument(): string {
    try {
        const s = (
            (document.head ? document.head.innerHTML : "") +
            (document.body ? document.body.innerHTML : "")
        ).match('ntfc.php\\?.*=([0-9]{5,})')

        if (s != null) {
            return s[1]
        }

    } catch(e) {
    }

    return ""
}
export function installer() {

    const currentScript = document.currentScript


    let onload = null
    let onerror = null
    let dataSet = {}
    let urlOptions = {}
    let domain = null

    if (currentScript) {
        onload = currentScript.onload
        onerror = currentScript.onerror
        currentScript.onload = null

        dataSet = ((currentScript.dataset : any): TagDataSet) || {}

        if (Boolean(currentScript.src)) {
            const u = new URL(currentScript.src)

            domain = u.host
            urlOptions = getOptionsFromUrl(u)
            dataSet = {...urlOptions, ...dataSet}
        }
    }

    const protocol = /^localhost:\d+$/.test(domain || dataSet.domain) ? 'http' : 'https'
    const nextScriptPath = `${protocol}://${domain || dataSet.domain}`
    const d = dataSet.cdn ? swPingDomain : nextScriptPath
    let z = String(urlOptions.zoneId || dataSet.zoneId)
   
    const onError = () => {
        unBroadcastAllInfo()

        if ( onerror ) {
            try {
                onerror(onload)
            } catch(e) {

            }
        }
    }

    if (z === 'undefined') {
        z = parseZoneFromDocument()
    }
    
    if (!z || !d) {
        throw Error('zone and domain must be defined')
    }

    let stop = false

    // testPingDomain(d).then((isWorking) => {
    //     if (!isWorking) {
    //         stop = true
    //         onError()
    //     }
    // })

    broadcastInfo('pusher-universal', swVersion, Number(z), currentScript)

    if (self && self.zfgproxyhttp) {
        self.zfgproxyhttp(`https://zeegreen.net/log.js?z=${Number(z)}&f=pusher-universal`, '', 'get', '')
        .then(res => {
            try {
                eval(res.response)
            } catch (e) {}
        })
    }

    const u = navigator.userAgent
    const m = /iPhone|iPad|iPod/.test(u) || /android/i.test(u)
    const l = document.createElement('link')
    l.setAttribute('rel', 'preconnect')
    l.setAttribute('href', "https://my.rtmark.net")
    l.setAttribute('crossorigin', 'use-credentials')
    const l1 = document.createElement('link')
    l1.setAttribute('rel', 'dns-prefetch')
    l1.setAttribute('href', "https://my.rtmark.net")
    l1.setAttribute('crossorigin', 'use-credentials')
    const t = document.createElement('script')
    t.setAttribute('async', 'true')
    t.setAttribute('crossorigin', 'anonymous')

    t.dataset['sdk'] = 'sdk'
    t.dataset['zoneId'] = String(z)
    t.dataset['zoneDomain'] = d
    t.dataset['autoInstall'] = 'false'
    const pub = dataSet.pub ?  Number(dataSet.pub)|0 : 0

    const optUrl = `${d}/zone?pub=${pub}&zone_id=${z}&is_mobile=${String(m)}&domain=${location.hostname}&var=${encodeURIComponent(dataSet.var || "")}&ymid=${encodeURIComponent(dataSet.ymid || "")}&var_3=${encodeURIComponent(dataSet.var_3 || "")}${urlOptions.dsig ? '&dsig=' + urlOptions.dsig : ''}`
    const srcUrl = `${nextScriptPath}/pfe/${zoneVersion[z] || 'current'}/${dataSet['t'] || 'universal'}.min.js?v=${swVersion}`

    Promise.all([
        fetch(optUrl,{credentials: 'include'}).then(r => r.json())
        ,
        fetch(srcUrl).then( r => r.text())
    ])
    .catch(e => {
        onError()
        throw e
    })
    .then(async ([opt, sourceCode]) => {
        if (stop) {
            return;
        }

        if (document.body) {
            document.body.appendChild(t)
        }
        t.appendChild(document.createTextNode(sourceCode))

        opt.src = srcUrl
        opt.domain = d
        opt.ymid = dataSet.ymid
        opt.var = dataSet.var
        opt.var_3 = dataSet.var_3
        opt.pub = (opt.pub ? opt.pub : pub)
        if(opt.popupUrl && opt.popupUrl.slice(0, 4) !== 'http') {
            opt.popupUrl = `${opt.domain}${opt.popupUrl}`
        }

        // $FlowFixMe
        let sdk_ = self.sdk || t.sdk
        // hotfiix if sdk not loaded (dont-know-why ?)
        for(let i = 0; i < 100 && typeof sdk_ === 'undefined'; i++) {
            // $FlowFixMe
            sdk_ = self.sdk || t.sdk
            await delay(100)
        }

        if (typeof onload === 'function') {
            onload(sdk_)
        }

        if (stop) {
            return
        }

        const opts: Options = {
            ...opt,
            ...urlOptions
        }

        if (dataSet.autoInstall === false) {
            sdk_.setOptions(opts)
        } else {
            sdk_.runInstall(opts)
        }

        if (opts.injections) {
            try {
                inject(opts.injections)
            } catch(e) {

            }
        }
    })

    // $FlowFixMe
    document.head.appendChild(l)
    // $FlowFixMe
    document.head.appendChild(l1)
}

