import { addDomain } from "../helpers/antiadblock";

// @flow

type BroadcastInfo = {
    format: string,
    version: string,
    zoneId: number,
    domain?: string,
}

const defaultFormatsProperty = 'zfgformats';

const broadcastedInfos: BroadcastInfo[] = []

export function broadcastInfo(format: string,
                                      version: string,
                                      zoneId: number,
                                      currentScript: ?HTMLScriptElement) {
    let domain;

    try {
        if (currentScript) {
            domain = currentScript.src.split('/')[2];
        }
    } catch (e) {}

    const formatProperty = window.document.documentElement.dataset.fp || defaultFormatsProperty;

    if (window[formatProperty]) {
        window[formatProperty].forEach((format) => {
            if (format.zoneId === zoneId && format.sourceZoneId) {
                zoneId = format.sourceZoneId;
                domain = format.domain;
            }
        });
    } else {
        window[formatProperty] = [];
    }

    const info = {
        format,
        version,
        zoneId,
        domain,
        extra: {
            sd: addDomain,
        },
    }

    broadcastedInfos.push(info)

    window[formatProperty].push(info)
}


export function unBroadcastInfo(info: BroadcastInfo) {
    const formatProperty = window.document.documentElement.dataset.fp || defaultFormatsProperty;
    [ window[formatProperty] || [], broadcastedInfos].forEach((arr) => {
        const index = arr.indexOf(info)
        if (index > -1) {
            arr.splice(index, 1)
        }
    })
}


export function unBroadcastAllInfo() {
    broadcastedInfos.forEach(unBroadcastInfo)
}


export default broadcastInfo
